        isPunish: "", //
        isSuspended: 0, //
        dangerLevel: "", //
        dangerType: "", //
        dangerSource: "", //
        acceptanceCharge: [], //
        rectificationCharge: [], //
        punishMoney: "", //
        dangerDesc: "" //
      },
      isPunishColumns: ["", ""],
      isSuspendedColumns: ["", ""],
      dangerSourceColumns: [
        {
          text: "",
          key: "XWGCK"
        },
        {
          text: "/",
          key: "YHSBK"
        },
        {
          text: "",
          key: "AQJCJL"
        },
        {
          text: "",
          key: "CBSYT"
        }
      ], //
      dangerLevelColumns: ["", "", ""], //
      dangerTypeColumns: [
        "",
        "/",
        "/",
